import React from "react";
//import { Link, link } from "react-router-dom";
import { Link } from "react-router-dom";

const Navbar = () => {
	return (
		<>
			<nav>
				<ul>
					<li>
						<Link to="/">Home</Link>
					</li>

					<li>
						<Link to="/about">About</Link>
					</li>

					<li>
						<Link to="/article-list">Articles List</Link>
					</li>
				</ul>
			</nav>
		</>
	);
};

export default Navbar;
